import React, {Component} from 'react'
import { Switch, Route, withRouter } from "react-router-dom";

import Song from './Song'
import "../styles/Game.scss"
import Sidebar from './Sidebar'
import Search from './Search'
import Playlist from './Playlist'

class Game extends Component {
    constructor(props) {
        super(props)
        this.state = {
            playlist: []
        }
        this.addSong = this.addSong.bind(this)
        this.removeSong = this.removeSong.bind(this)
        this.songInPlaylist = this.songInPlaylist.bind(this)

    }

    songInPlaylist(song) {
        return this.state.playlist.slice().map((s)=>JSON.stringify(s)).includes(JSON.stringify(song))
    }

    addSong(song) {
        if(this.songInPlaylist(song)) return;

        var playlist = this.state.playlist;
        playlist.push(song);
        this.setState({playlist: playlist})
    }

    removeSong(index) {
        var playlist = this.state.playlist;
        playlist.splice(index, 1);
        this.setState({playlist: playlist})
    }

    render() {
        return (
            <div id="game">
                <Sidebar>
                    {/* <div id="menu"> */}
                        <Search addSong={this.addSong} songInPlaylist={this.songInPlaylist}/>
                        <Playlist playlist={this.state.playlist} removeSong={this.removeSong}/>
                    {/* </div> */}
                </Sidebar>
                <Switch>
                    <Route
                        exact
                        path="/"
                        render={(props) => 
                            <div id="home">
                                <div className="big">How well do you know your favorite songs?</div>
                                <div>Start by expanding the sidebar and searching for a song to play.</div>
                                <div>Randomization coming soon.</div>
                            </div>}
                    />
                    <Route
                        path="/songs/:genius_id"
                        render={(props) => <Song {...props}/>}
                    />
                </Switch> 
            </div>
        );
    
    }
} export default withRouter(Game);