import React, {Component} from 'react'
// import { Input } from 'reactstrap'

import { FaTimesCircle as Remove, FaRandom as Shuffle } from 'react-icons/fa'

import '../styles/Search.scss'

class Playlist extends Component {

    constructor(props){
        super(props)
        this.state = {
            search: "",
            results: []
        }
        this.updateInput = this.updateInput.bind(this)
        this.getSearchResults = this.getSearchResults.bind(this)
        this.search = this.search.bind(this)
        this.listenForEnter = this.listenForEnter.bind(this)
    }

    getSearchResults(obj) {
        console.log(obj)
        let results = obj.response.hits.map((hit) => {return {title: hit.result.title, artist: {name: hit.result.primary_artist.name, id: hit.result.primary_artist.id}, lyrics_path: hit.result.path, song_path: hit.result.api_path, image: hit.result.song_art_image_url}})
        console.log(results)
        this.setState({results: results})
    }

    getGenius(endpoint, callback) {
        var request = new XMLHttpRequest();
        request.open('GET', 'https://api.genius.com' + endpoint + '&access_token=4qtBMiQeR5pD1zFm-vGmFV6j5khGAiRQskTCLXyuGbxeYGbnXrTnXIyA5n2iXjdg', true);
        request.onreadystatechange = () => {
            if (request.readyState === 4 && request.status === 200) {
                var type = request.getResponseHeader('Content-Type');
                if (type.indexOf("text") !== 1) {
                    console.log(JSON.parse(request.responseText))
                    callback(JSON.parse(request.responseText))
                }
            }
        }
        request.send();
    }

    renderSearch(){
        return(
            this.state.results.map((result)=><div><a href={"/#" +result.song_path}><button>{result.title} by <i>{result.artist}</i></button></a></div>)
        )

    }
    updateInput(event) {
        this.setState({search: event.target.value})
    }

    search() {
        this.getGenius('/search?per_page=15&q='+encodeURI(this.state.search), this.getSearchResults)
    }

    listenForEnter(event) {
        if (event.keyCode === 13) {
            this.search();
        }
    }

    getRandomSong() {
        return this.props.playlist.filter((song)=> JSON.stringify(song) !== JSON.stringify(this.state.song))[Math.floor(Math.random() * this.props.playlist.length)].song_path
    }

    render(){
        return (
            <div id="playlist">
                <div id="heading">
                    My Playlist
                    {this.props.playlist.length > 0 && <Shuffle className="button" onClick={()=>{window.location.href = "/#" + this.getRandomSong()}}/>}
                </div>
                <div id="search_results">
                    {this.props.playlist.length > 0 ? this.props.playlist.map((result, index)=>
                        <div className="result" key={result.song_path}>
                            <div className="info">
                                <img src={result.image} alt=""/>
                                <div className="text">
                                    <a className="song" title="Play this song" href={"/#" +result.song_path}>{result.title}</a> 
                                    <a className="artist" title="View more from this artist" href={"/#" + result.artist.id}><i>{result.artist.name}</i></a>
                                </div>
                            </div>
                            <Remove title="Add to playlist" className="button" onClick={()=>{this.props.removeSong(index)}}/>
                        </div>
                    ) : <div id="alt">Search for songs to add to your playlist!</div>}
                </div>

            </div>
        );
    
    }
} export default Playlist;